@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: rgba(0, 0 , 0, .65) url(/static/media/crikvenica-background.d1f96862.png);
  background-blend-mode: darken;
  background-position: center;
  background-size: cover;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  color: #fff;
  min-height: 100vh;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #fff;
}

.App-h1 {
  text-align: right;
  position: absolute;
  top: 10%;
  left: 45%;
  font-size: 45px;
  font-family: 'Montserrat', sans-serif;
  -webkit-transition: 2s;
  transition: 2s;
}

.App-h1:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

button {
  position: absolute;
  top: 85%;
  left: 85%;
  height: 50px;
  width: 125px;
  border-radius: 7px;
  background: transparent;
  color: #fff;
  -webkit-transition: 1.2s;
  transition: 1.2s;
}

button:hover {
   background: #b6995a;
   color: #000;
   border: none;
}

.App-link {
  color: #09d3ac;
}

.first-img {
  position: absolute;
  left: 42%;
}

.second-img {
  position: absolute;
  left: 62%;
}


.third-img {
  position: absolute;
  left: 22%;
}

.h1-second {
  text-align: center;
}

.main-content {
   text-align: right;
   margin-right: 115px;
   font-size: 15px;
}

#navbar {
   overflow: hidden;
   background-color: #333333;
   top: 0;
   width: 100%;
}

#navbar a {
   display: block;
   float: left;
   color: #ffffff;
   text-align: center;
   padding: 14px 16px;
   text-decoration: none;
   font-size: 18px;
}

#navbar a:hover {
   background-color: #d4af37;
   }

.App-three {
     margin-top: 250px;
     margin-right: 750px;
}

table, th, td {
  border: 1px solid #000000;
}

